import request from '@/utils/request';
var api = {
  GetById: '/api/Company/Employee/GetById',
  Create: '/api/Company/Employee/create',
  UpdateInfo: '/api/company/Employee/UpdateInfo',
  GetEmployeesByName: '/api/Company/Employee/GetEmployeesByName',
  DeleteEmployee: '/api/Company/Employee/delete',
  GetEmployeesBellowRoleLevel: '/api/company/employee/GetEmployeesBellowRoleLevel',
  UploadEmployee: '/api/company/Employee/UploadEmployee',
  GetDevicesByEmpId: '/api/dms/Device/GetDevicesByEmpId',
  UpdateByDeviceRefEmploee: '/api/dms/Device/UpdateByDeviceRefEmploee'
};
export function GetById(id) {
  return request({
    url: api.GetById,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: {
      id: id
    }
  });
}
export function Create(obj) {
  return request({
    url: api.Create,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}
export function UpdateInfo(obj) {
  return request({
    url: api.UpdateInfo,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}
export function DeleteEmployee(id) {
  return request({
    url: api.DeleteEmployee,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: {
      id: id
    }
  });
}
export function GetEmployeesByName(obj) {
  return request({
    url: api.GetEmployeesByName,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: obj
  });
}
export function GetEmployeesBellowRoleLevel(obj) {
  return request({
    url: api.GetEmployeesBellowRoleLevel,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: obj
  });
} // 员工导入

export function UploadEmployee(excel) {
  return request({
    url: api.UploadEmployee,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: excel
  });
} // 获取员工的设备列表 

export function GetDevicesByEmpId(id) {
  return request({
    url: api.GetDevicesByEmpId,
    method: 'get',
    params: id
  });
} // 通过选择设备关联员工 

export function UpdateByDeviceRefEmploee(obj) {
  return request({
    url: api.UpdateByDeviceRefEmploee,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}