import request from '@/utils/request';
var api = {
  GetAll: '/api/company/Role/GetAll',
  GetById: '/api/company/Role/GetById',
  Create: '/api/company/Role/Create',
  deleteRole: '/api/company/Role/delete',
  GetAllFunction: '/api/Operation/Function/GetAllDmsFuncs',
  UpdateRoleRights: '/api/Company/Role/UpdateRoleRights',
  SaveRoleRights: '/api/company/Role/SaveRoleRights'
}; // 获取全部角色信息信息

export function GetAll() {
  return request({
    url: api.GetAll,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    }
  });
} // 根据id获取角色

export function GetById(id) {
  return request({
    url: api.GetById,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: {
      id: id
    }
  });
} // 新增角色

export function Create(obj) {
  return request({
    url: api.Create,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 删除角色

export function deleteRole(id) {
  return request({
    url: api.deleteRole,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: {
      id: id
    }
  });
}
/**
 * @description 修改角色
 * @param  {Object} obj
 */

export function update(obj) {
  return request({
    url: api.update,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 获取全部功能信息

export function GetAllFunction() {
  return request({
    url: api.GetAllFunction,
    method: 'get'
  });
} // 更新角色与权限信息

export function UpdateRoleRights(obj) {
  return request({
    url: api.UpdateRoleRights,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 新增保存角色

export function SaveRoleRights(obj) {
  return request({
    url: api.SaveRoleRights,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}